body {
  color: white;
}
.app {
  margin-top: 5%;
}
.main {
  margin-top: 5%;
  background-color: rgb(41, 41, 41);
  min-height: 500px;
  width: 450px;
  margin: auto;
  border-radius: 15px;
  padding-bottom: 20px;
}
#to-do-list textarea {
  background-color: rgb(19, 121, 146);
  color: #d2d2d2;
  min-width: 50px;
  max-width: 350px;
  min-height: 50px;
  width: 350px;
  height: 60px;
  border: 0;
  margin-top: 10%;
  margin-left: 12%;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 15px;
  border-radius: 10px;
}
#to-do-list textarea::placeholder {
  color: #d2d2d2;
  font-size: 15px;
  padding: 5px;
}
#to-do-list textarea:focus,
#to-do-list button:focus {
  outline: none;
}
#to-do-list button:hover {
  background-color: rgb(73, 73, 73);
}
#bMargin {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}
#to-do-list button {
  background-color: rgb(83, 83, 83);
  height: 35px;
  width: 70px;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  font-style: italic;
}
.items {
  border-top: 1px solid #d2d2d2;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
}
ul {
  margin-left: 0px;
}

li {
  background-color: rgb(204, 156, 24);
  border: none;
  border-radius: 5px;
  padding: 7px;
  width: 350px;
  margin-right: 10px;
}
.list div {
  height: 100px;
  float: right;
  cursor: pointer;
}
#pTag {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  margin-top: 0px;
}
#vertitalSlash {
  font-size: 300%;
  padding: 0px;
  padding-right: 5px;
  margin: 0px;
}
@media only screen and (max-width: 540px) {
}
@media only screen and (max-width: 452px) {
  li {
    width: 250px;
    margin-right: 20px;
  }
  .main {
    width: 350px;
  }
  #to-do-list textarea {
    max-width: 270px;
    min-height: 50px;
    width: 260px;
    height: 60px;
  }
}
@media only screen and (max-width: 314px) {
  .App {
    width: 280px;
  }
  #to-do-list textarea {
    max-width: 220px;
    min-height: 50px;
    width: 240px;
    height: 60px;
  }
  li {
    width: 200px;
  }
  #vertitalSlash {
    font-size: 200%;
  }
}
