body {
  background-color: rgb(52, 52, 54);
  background-image: url("skull_image.png");
  background-size: 100px;
  background-attachment: fixed;
}
#header {
  overflow: hidden;
  padding: 0px;
  background-color: rgb(41, 41, 41);
  width: 100%;
  max-height: 150px;
}
#headersort {
  width: 100%;
  display: block;
}
.HBtn {
  margin-top: 2%;
  margin-right: 2.5%;
  margin-left: 50%;
  float: right;
  display: flex;
  flex-direction: row;
}
.logoutBTN {
  background-color: rgb(236, 181, 29);
  color: white;
  height: 30px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}
.logoutBTN:hover {
  cursor: pointer;
}
.saveBTN {
  background-color: rgb(3, 204, 93);
  color: white;
  height: 30px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}
.saveBTN:hover {
  cursor: pointer;
}
.status {
  color: rgb(3, 204, 93);
  margin-right: 10px;
  margin-top: 5px;
}
#header h3 {
  float: left;
  color: #d2d2d2;
  font-style: italic;
  font-weight: bold;
  font-size: 25px;
}

.footer {
  overflow: hidden;
  margin-top: 10%;
  width: 100%;
  background-color: rgb(41, 41, 41);
  height: 70px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: #d2d2d2;
}
@media only screen and (max-width: 1080px) {
  #headersort {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (max-width: 540px) {
  .HBtn {
  }
  #headersort {
  }
}
@media only screen and (max-width: 452px) {
  #header {
  }
}

@media only screen and (max-width: 314px) {
  #header h3 {
    font-size: 20px;
  }
  #footer p {
    font-size: 15px;
  }
}
