.userLog {
  background-color: rgb(41, 41, 41);
  height: 400px;
  width: 350px;
  margin: auto;
  border: none;
  border-radius: 15px;
  margin-top: 3%;
  display: flex;
  flex-direction: column;
}
.form {
  padding: 10px;
}
.form h1 {
  text-align: center;
  font-style: italic;
  margin-bottom: 50px;
  margin-top: 30px;
}
.InputField {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.InputField input {
  width: 200px;
  height: 20px;
  background-color: rgb(19, 121, 146);
  color: white;
  border: none;
}
.InputField input::placeholder {
  color: white;
  padding-left: 5px;
  font-style: italic;
}
.InputField input:focus {
}
.btn {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
#login {
  width: 50px;
}
.btn #login:hover {
  cursor: pointer;
}
.form div {
  margin: 10px;
}

.userNote {
  width: 250px;
  font-style: italic;
}
